import React from 'react';
import {withTranslation} from "react-i18next";
import {withStyles} from "@material-ui/core";
import {compose} from "redux";
import {Route, Switch} from 'react-router-dom'
import FotoContent from "./foto/FotoContent";
import WindowContent from "./foto/FotoContent";
import MappingContent from "./mapping/MappingContent";
import {URL_FOTO, URL_MAPPING, URL_WINDOW} from "../utils/urlUtils";
import AppBar from "./navigation/AppBar";

const styles = ({
    appContainer: {
        height: '100vh',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between'
    }
})

function AppContainer({classes, t, ...props}) {

    function getTitle() {
        return 'Headline'
    }

    function getContentTitle() {
        switch (props.location.pathname) {
            case URL_FOTO:
                return t('foto')
            case URL_MAPPING:
                return t('mapping')
            case URL_WINDOW:
                return t('window')
            default:
                return t('headline')

        }
    }

    function getContentDescription() {
        return 'Beschreibungstext, Beschreibungstext, Beschreibungstext, Beschreibungstext'
    }

    return (
        <div className={classes.appContainer}>
            <AppBar title={getTitle()} contentTitle={getContentTitle()}
                    contentDescription={getContentDescription()}/>
            <div>

                <Switch>
                    <Route path={URL_FOTO} component={FotoContent}/>
                    <Route path={URL_MAPPING} component={MappingContent}/>
                    <Route path={URL_WINDOW} component={WindowContent}/>
                    <Route path={'/'} component={FotoContent}/>
                </Switch>
            </div>
        </div>
    );
}

export default compose(withStyles(styles), withTranslation())(AppContainer)

