import PropTypes from 'prop-types';
import {Typography, withStyles} from "@material-ui/core";
import {compose} from "redux";
import {withTranslation} from "react-i18next";
import NavigationBackSvgIcon from "../common/icons/NavigationBackSvgIcon";
import clsx from "clsx";

const styles = theme => ({
    root: {
        display: 'flex',
        margin: 'auto 0',
        alignSelf: 'flex-start',
        alignItems: 'center',
        cursor: 'pointer',
    },
    icon: {
        marginRight: '5px',
        width: '32px',
        height: '32px',
    },
})

function BackNavigation({classes, t, backLabel, onNavigateBack, className}) {
    return (
        <div className={clsx(classes.root, className)} onClick={onNavigateBack}>
            <NavigationBackSvgIcon className={classes.icon}/>
            <Typography variant={'h6'}>
                {backLabel ? backLabel : t("back")}
            </Typography>
        </div>
    )
}

BackNavigation.propTypes = {
    backLabel: PropTypes.string,
    onNavigateBack: PropTypes.func.isRequired,
}

export default compose(withStyles(styles), withTranslation())(BackNavigation)