import {withStyles} from "@material-ui/core";
import {compose} from "redux";
import {withTranslation} from "react-i18next";
import React from "react";
import BottomSpreadsheet from "../BottomSpreadsheet";
import PropTypes from "prop-types";

const styles = theme => ({
    root: {
        height: '100%',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between'
    },
})

function SpreadsheetWrapper({classes, t, spreadsheetExpandable, ...props}) {
    return (
        <div className={classes.root}>
            {props.children}
            <BottomSpreadsheet expandable={spreadsheetExpandable}/>
        </div>
    )
}

SpreadsheetWrapper.propTypes = {spreadsheetExpandable: PropTypes.bool}

export default compose(withStyles(styles), withTranslation())(SpreadsheetWrapper)
