import {compose} from "redux";
import {withStyles} from "@material-ui/core";
import {withTranslation} from "react-i18next";
import PropTypes from "prop-types";
import Expandable from "../common/Expandable";
import TopLevelInformation from "../common/TopLevelInfomation";
import clsx from "clsx";
import AppBarExpandedContent from "./AppBarExpandedContent";
import NavArrowDownSvgIcon from "../common/icons/NavArrowDownSvgIcon";
import BreadcrumbsMenu from "./submenu/BreadcrumbsMenu";

const styles = theme => ({
    root: {
        minHeight: theme.skeleton.appBar.height,
        background: theme.colors.palette.corporate.yellowMain,
        padding: theme.skeleton.appBar.padding,
        borderRadius: '0 0 0 30px',
        boxSizing: 'border-box'
    },
    title: {
        marginTop: '28px'
    },
    actions: {
        display: 'flex',
        alignItems: 'center'
    },
})

function AppBar({classes, t, title, contentTitle, contentDescription, ...props}) {
    return (
        <div className={clsx(classes.root)}>
            <TopLevelInformation onNavigateBack={() => console.log("Back")} backLabel={'Zurck'} title={title}/>
            <Expandable clickElement={<NavArrowDownSvgIcon className={clsx(classes.icon)}/>}
                        additionalElement={<BreadcrumbsMenu/>}>
                <AppBarExpandedContent title={contentTitle} text={contentDescription}/>
            </Expandable>
        </div>
    )
}

AppBar.propTypes = {
    title: PropTypes.string.isRequired,
    contentTitle: PropTypes.string,
    contentDescription: PropTypes.string,
}

export default compose(withStyles(styles), withTranslation())(AppBar)