import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import {IconButton, Typography, useTheme, withStyles} from "@material-ui/core";
import * as theme from "../../../theme";

const styles = theme => ({
    root: {
        display: 'flex',
        borderRadius: '12px',
        height: '48px',
        backgroundColor: theme.colors.palette.corporate.yellowMain,
        boxShadow: '0 2px 14px 0 rgba(210, 179, 13, 0.42)',
        border: 'solid 1px rgba(255, 193, 0, 0.5)'
    },
    icon: {
        width: '2.4rem',
        height: '2.4rem',
        overflow: 'hidden',
    },
});

function ColoredIconButton({
                               textColor,
                               background,
                               icon,
                               text,
                               onClick,
                               classes,
                               className,
                               fullWidth,
                               disabled,
                               halfWidth,
                               ...props
                           }) {

    const theme = useTheme()
    textColor = textColor ? textColor : theme.colors.palette.neutral.black
    return (
        <IconButton className={clsx(classes.root, className)}
                    onClick={onClick}
                    {...props}
                    disabled={disabled}
                    style={{
                        background: disabled ? theme.colors.palette.neutral.grayMin : background,
                        width: fullWidth ? '100%' : halfWidth ? '50%' : '',
                    }}>
            {icon ? <div
                className={classes.icon}>{React.cloneElement(icon, {stroke: textColor})}</div> : null}
            {text ? <Typography variant={'h2'} style={{color: textColor, marginLeft: '7px'}}>{text}</Typography> : null}
        </IconButton>
    );
}

ColoredIconButton.propTypes = {
    text: PropTypes.string,
    icon: PropTypes.element,
    textColor: PropTypes.string,
    background: PropTypes.string,
    onClick: PropTypes.func,
}

ColoredIconButton.defaultProps = {
    textColor: theme.colors.palette.neutral.black,
    background: theme.colors.palette.corporate.yellowMain,
}

export default withStyles(styles)(ColoredIconButton);