import {withStyles} from "@material-ui/core";
import {compose} from "redux";
import {withTranslation} from "react-i18next";
import React, {useState} from "react";
import PropTypes from "prop-types";
import clsx from "clsx";
import ExpandSvgIcon from "./icons/ExpandSvgIcon";

const styles = theme => ({
    root: {},
    actions: {
        marginTop: '17px',
        display: 'flex',
        alignItems: 'center'
    },
    expandIcon: {
        display: 'flex',
        justifyContent: 'center',
        width: '100%'
    },
    rotate: {
        transform: 'rotate(180deg)'
    },
    icon: {
        marginRight: '6px'
    }
})

function Expandable({classes, t, clickElement, additionalElement, ...props}) {
    const [expanded, setExpanded] = useState(false);

    return (
        <div className={classes.root}>
            <div className={additionalElement ? classes.actions : classes.expandIcon}>
                {clickElement
                    ? React.cloneElement(clickElement, {
                        onClick: () => setExpanded(!expanded),
                        className: expanded ? classes.rotate : '',
                    })
                    :
                    <ExpandSvgIcon onClick={() => setExpanded(!expanded)}
                                   className={clsx(classes.icon, expanded ? classes.rotate : '')}/>
                }
                {additionalElement}
            </div>
            {expanded && props.children}
        </div>
    )
}

Expandable.propTypes = {
    clickElement: PropTypes.element,
    additionalElement: PropTypes.element,
}

export default compose(withStyles(styles), withTranslation())(Expandable)