import {compose} from "redux";
import {Typography, withStyles} from "@material-ui/core";
import {withTranslation} from "react-i18next";
import PropTypes from "prop-types";
import clsx from "clsx";

const styles = theme => ({
    root: {
        paddingTop: '10px',
        boxSizing: 'border-box',
        height: theme.skeleton.appBar.heightExpandedContent
    },
    title: {}
})

function AppBarExpandedContent({classes, t, title, text, ...props}) {
    return (
        <div className={clsx(classes.root)}>
            <Typography className={classes.title} variant={'h1'}>{title}</Typography>
            <Typography variant={'h6'}>
                {text}
            </Typography>
        </div>
    )
}

AppBarExpandedContent.propTypes = {
    title: PropTypes.string.isRequired,
    text: PropTypes.string.isRequired
}

export default compose(withStyles(styles), withTranslation())(AppBarExpandedContent)