import PropTypes from 'prop-types';
import {Typography, withStyles} from "@material-ui/core";
import BackNavigation from "../navigation/BackNavigation";
import BurgerSvgIcon from "./icons/BurgerSvgIcon";
import clsx from "clsx";

const styles = theme => ({
    root: {
        display: 'flex',
        alignItems: 'center'
    },
    flexItem: {
        flex: '1 1 0px'
    },
    burgerIcon: {
        display: 'flex',
        justifyContent: 'flex-end'
    },
    title: {
        display: 'flex',
        justifyContent: 'center'
    }
})

function TopLevelInformation({classes, title, backLabel, onNavigateBack, className, ...props}) {
    return (
        <div className={clsx(classes.root, className)}>
            <BackNavigation backLabel={backLabel} onNavigateBack={onNavigateBack} className={classes.flexItem}/>
            <Typography variant={'h2'} className={clsx(classes.flexItem, classes.title)}>
                {title}
            </Typography>
            <div className={`${classes.flexItem} ${classes.burgerIcon}`}>
                <BurgerSvgIcon/>
            </div>
        </div>
    )
}

TopLevelInformation.propTypes = {
    title: PropTypes.string.isRequired,
    backLabel: PropTypes.string.isRequired,
    onNavigateBack: PropTypes.func.isRequired
}

export default withStyles(styles)(TopLevelInformation)