import {Breadcrumbs, withStyles} from "@material-ui/core";
import {compose} from "redux";
import {withTranslation} from "react-i18next";
import {URL_FOTO, URL_MAPPING, URL_WINDOW} from "../../../utils/urlUtils";
import {useHistory} from "react-router-dom";
import * as React from 'react';
import {useState} from 'react';
import Breadcrumb from "./Breadcrumb";

const styles = theme => ({
    root: {},
    menuItem: {
        cursor: 'pointer'
    }
})

function BreadcrumbsMenu({classes, t, ...props}) {
    let history = useHistory();
    const [activeBreadCrumb, setActiveBreadCrumb] = useState(2);

    function onBreadcrumbClick(breadcrumbIndex, url) {
        setActiveBreadCrumb(breadcrumbIndex)
        route(url)
    }

    function route(url) {
        history.push(url)
    }

    return (
        <div className={classes.root}>
            <Breadcrumbs aria-label="breadcrumb">
                <Breadcrumb
                    active={activeBreadCrumb === 0}
                    label={t('foto')}
                    onClick={() => onBreadcrumbClick(0, URL_FOTO)}
                />
                <Breadcrumb
                    active={activeBreadCrumb === 1}
                    label={t('mapping')}
                    onClick={() => onBreadcrumbClick(1, URL_MAPPING)}
                />
                <Breadcrumb
                    active={activeBreadCrumb === 2}
                    label={t('window')}
                    onClick={() => onBreadcrumbClick(2, URL_WINDOW)}
                />
            </Breadcrumbs>
        </div>
    )
}

export default compose(withStyles(styles), withTranslation())(BreadcrumbsMenu)