import {IconButton, withStyles} from "@material-ui/core";
import PropTypes from "prop-types";
import clsx from "clsx";
import React from "react";
import * as theme from "../../../theme";

const styles = theme => ({
    root: {
        borderRadius: '50%',
        width: '40px',
        height: '40px',
        border: 'solid 1px ' + theme.colors.palette.neutral.grayMin,
        boxShadow: '0 1px 4px 0 rgba(0, 0, 0, 0.15)'
    },
    icon: {
        width: '32px',
        height: '32px',
    }
})

function IconButtonRounded({classes, icon, className, onClick, disabled, background, iconColor, ...props}) {
    return (
        <IconButton className={clsx(classes.root, className)}
                    onClick={onClick}
                    {...props}
                    disabled={disabled}>
            {icon &&
            <div className={classes.icon}>
                {React.cloneElement(icon, {stroke: disabled ? theme.colors.palette.neutral.grayMin : iconColor})}
            </div>}
        </IconButton>
    )
}

IconButtonRounded.prototype = {
    icon: PropTypes.any.isRequired,
    onClick: PropTypes.func,
    iconColor: PropTypes.string,
    background: PropTypes.string,
}


IconButtonRounded.defaultProps = {
    iconColor: theme.colors.palette.neutral.black,
    background: theme.colors.palette.neutral.white,
}


export default withStyles(styles)(IconButtonRounded)