import {Chip, withStyles} from "@material-ui/core";
import {compose} from "redux";
import {withTranslation} from "react-i18next";
import * as React from 'react';
import clsx from "clsx";

const styles = theme => ({
    root: {
        height: '25px',
        backgroundColor: theme.colors.palette.corporate.yellowMain,
        color: theme.palette.text.primary,
        '&:active': {
            backgroundColor: theme.colors.palette.corporate.yellowMain,
        },
        '&:hover': {
            backgroundColor: theme.colors.palette.corporate.yellowDark,
        },
        '&:focus': {
            backgroundColor: theme.colors.palette.corporate.yellowDark,
        }
    },
    active: {
        backgroundColor: theme.colors.palette.corporate.yellowDark,
        '& .MuiChip-label': {
            fontFamily: theme.typography.h2.fontFamily
        }
    }
})

function Breadcrumb({classes, t, active, label, onClick, ...props}) {

    return (
        <Chip className={clsx(classes.root, active ? classes.active : '')} label={label} onClick={onClick}/>
    )
}

export default compose(withStyles(styles), withTranslation())(Breadcrumb)