import {compose} from "redux";
import {Divider, withStyles} from "@material-ui/core";
import {withTranslation} from "react-i18next";
import PropTypes from "prop-types";
import clsx from "clsx";

const styles = theme => ({
    root: {
        paddingTop: '18px',
        paddingBottom: '30px',
        boxSizing: 'border-box',
        height: theme.skeleton.bottomSpreadsheet.heightExpandedContent,
    },
    content: {
        height: '100%'
    },
    divider: {}
})

function AppBarExpandedContent({classes, t, title, text, ...props}) {
    return (
        <div className={clsx(classes.root)}>
            <div className={classes.content}>

            </div>
            <Divider className={classes.divider}/>
        </div>
    )
}

AppBarExpandedContent.propTypes = {
    title: PropTypes.string.isRequired,
    text: PropTypes.string.isRequired
}

export default compose(withStyles(styles), withTranslation())(AppBarExpandedContent)
